  const hexToRgba = (hex, alpha = 1) => {
    const bigint = parseInt(hex.replace("#", ""), 16);
    const r = (bigint >> 16) & 255;
    const g = (bigint >> 8) & 255;
    const b = bigint & 255;
    return `rgba(${r}, ${g}, ${b}, ${alpha})`;
  };

  // Helper function to convert named color or any valid CSS color to HSL
  const hexToHSL = (hex) => {
    // Remove the hash (#) if it's there
    hex = hex.replace(/^#/, "");
  
    // Parse the r, g, b values
    let r = parseInt(hex.substring(0, 2), 16) / 255;
    let g = parseInt(hex.substring(2, 4), 16) / 255;
    let b = parseInt(hex.substring(4, 6), 16) / 255;
  
    // Find the maximum and minimum values of r, g, and b
    const max = Math.max(r, g, b);
    const min = Math.min(r, g, b);
    const delta = max - min;
  
    let h = 0; // Hue
    let s = 0; // Saturation
    const l = (max + min) / 2; // Lightness
  
    if (delta !== 0) {
      // Calculate saturation
      s = delta / (1 - Math.abs(2 * l - 1));
  
      // Calculate hue
      switch (max) {
        case r:
          h = ((g - b) / delta + (g < b ? 6 : 0)) * 60;
          break;
        case g:
          h = ((b - r) / delta + 2) * 60;
          break;
        case b:
          h = ((r - g) / delta + 4) * 60;
          break;
      }
    }
  
    h = Math.round(h);
    s = Math.round(s * 100);
    const lightness = Math.round(l * 100);
  
    return `hsl(${h}, ${s}%, ${lightness}%)`;
  };


  function redToBlueGradient(frequencyRatio) {
      // Clamp frequencyRatio to [0, 1]
      frequencyRatio = Math.max(0, Math.min(1, frequencyRatio));

      // Define color stops (Red  Orange  Yellow  Purple  Blue  Cyan)
      const colors = [
        [255, 0, 0],     // Red
        [255, 165, 0],   // Orange
        [255, 255, 0],   // Yellow
        [128, 0, 128],   // Purple
        [0, 0, 255],     // Blue
        [0, 255, 255]    // Cyan
      ];

      // Number of color stops
      const numStops = colors.length - 1;

      // Find the current stop and local ratio
      const stopIndex = Math.min(Math.floor(frequencyRatio * numStops), numStops - 1); // Prevent out-of-bounds
      const localRatio = (frequencyRatio * numStops) - stopIndex; // Ratio within this segment

      // Start and end colors
      const [r1, g1, b1] = colors[stopIndex] || [0, 0, 0];       // Fallback to black if undefined
      const [r2, g2, b2] = colors[stopIndex + 1] || [0, 0, 0];   // Fallback to black if undefined

      // Linear interpolation for RGB
      const r = Math.round(r1 + (r2 - r1) * localRatio);
      const g = Math.round(g1 + (g2 - g1) * localRatio);
      const b = Math.round(b1 + (b2 - b1) * localRatio);

      return `rgb(${r}, ${g}, ${b})`;
  }

