      if (visualState.state > 1) {
        let amplitudeSumUnder100Hz = 0;
        let validFrequencyCount = 0;
      
        for (let i = 0; i < bufferLength; i++) {
          const frequency = (i * sampleRate) / (2 * bufferLength);
          if (frequency < maxFrequency) {
            const weightedAmplitude = dataArray[i] * getAWeighting(frequency);
            amplitudeSumUnder100Hz += weightedAmplitude;
            validFrequencyCount++;
          }
        }
      
        // Calculate the average amplitude
        const averageAmplitude = validFrequencyCount > 0 ? amplitudeSumUnder100Hz / validFrequencyCount : 0;
      
        // Normalize the amplitude to a range of 0 to 150
        const minAmplitude = 50; // Minimum amplitude threshold
        const maxAmplitude = 150; // Maximum amplitude threshold
        const normalizedAmplitude = Math.max(0, Math.min(1, (averageAmplitude - minAmplitude) / (maxAmplitude - minAmplitude)));

      
        // Map the normalized amplitude to a line thickness range
        const minLineWidth = 1;  // Minimum line thickness
        const maxLineWidth = 7;  // Maximum line thickness
        lineWidthUnder100Hz = minLineWidth + normalizedAmplitude * (maxLineWidth - minLineWidth);
      
      }

